.wickedpicker {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    box-shadow: 0 5px 30px rgba(1#9299b8, .15);
    background: #fff;
    margin: 0 auto;
    border-radius: 6px;
    width: 270px;
    height: 130px;
    font-size: 14px;
    display: none
}

.wickedpicker__title {
    background-image: -webkit-linear-gradient(top, #fff 0, #f2f2f2 100%);
    background-image: linear-gradient(to bottom, #fff 0, #f2f2f2 100%);
    position: relative;
    font-size: 14px;
    font-weight: 500;
    background: #f4f5f7;
    margin: 0 auto;
    padding: 12px 11px 10px 15px;
    color: #272b41;
    font-size: inherit
}

.wickedpicker__close {
    font-family: "Line Awesome Free";
    font-weight: 900;
    -webkit-transform: translateY(-25%);
    -moz-transform: translateY(-25%);
    -ms-transform: translateY(-25%);
    -o-transform: translateY(-25%);
    transform: translateY(-25%);
    position: absolute;
    top: 25%;
    right: 10px;
    color: #34495e;
    cursor: pointer
}

.wickedpicker__close:before {
    content: '\f00d'
}

.wickedpicker__controls {
    padding: 10px 0;
    line-height: normal;
    color: #868eae;
    margin: 0
}

.wickedpicker__controls__control,
.wickedpicker__controls__control--separator {
    vertical-align: middle;
    display: inline-block;
    font-size: inherit;
    margin: 0 auto;
    width: 40px;
    letter-spacing: 1.3px
}

.wickedpicker__controls__control-down,
.wickedpicker__controls__control-up {
    color: #34495e;
    position: relative;
    display: block;
    margin: 3px auto;
    font-size: 18px;
    cursor: pointer
}

.wickedpicker__controls__control-up:before {
    content: '\f106'
}

.wickedpicker__controls__control-down:after {
    content: '\f107'
}

.wickedpicker__controls__control--separator {
    width: 5px
}

.text-center,
.wickedpicker__controls,
.wickedpicker__controls__control,
.wickedpicker__controls__control--separator,
.wickedpicker__controls__control-down,
.wickedpicker__controls__control-up,
.wickedpicker__title {
    text-align: center
}

.hover-state {
    color: #3498db
}

.fontello-after:after,
.fontello:before,
.wickedpicker__controls__control-down:after,
.wickedpicker__controls__control-up:before {
    font-family: "Line Awesome Free";
    font-style: normal;
    font-weight: 900;
    display: inline-block;
    text-decoration: inherit;
    width: 1em;
    margin-right: .2em;
    text-align: center;
    font-variant: normal;
    text-transform: none;
    line-height: 1em;
    margin-left: .2em
}