.round {
    position: relative;
    margin-bottom: 10px;
  }

.table_colour{
    color: #7a7a7a !important;
    font-weight: 400 !important;
}
  
  .round label {
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 50%;
    cursor: pointer;
    height: 26px;
    left: 0;
    position: absolute;
    top: 0;
    width: 26px;
  }
  
  .round label:after {
    border: 2px solid #fff;
    border-top: none;
    border-right: none;
    content: "";
    height: 6px;
    left: 6px;
    opacity: 0;
    position: absolute;
    top: 8px;
    transform: rotate(-45deg);
    width: 12px;
  }
  
  .round input[type="checkbox"] {
    visibility: hidden;
  }
  
  .round input[type="checkbox"]:checked + label {
    background-color: #f8a046;
    border-color: #f8a046;
  }
  
  .round input[type="checkbox"]:checked + label:after {
    opacity: 1;
  }
  

