    float: right;
    width:1em;
    overflow:hidden;
    white-space:nowrap;
    cursor:pointer;
    font-size:22px;
    background-color: #fff3d0;
    border-radius: 5px;
    height: 35px;
    width: 35px;
    color: #000;
    line-height: 35px;
    margin-inline-end: 8px;
    text-align: center;
        transition: all 0.2s,outline 0s;
}
.rate:not(:checked) > label:before {
    content: '';
}
.rate > input:checked ~ label {
    color: #000;  
    background-color: #ffbc00;
    border-radius: 5px;
    height: 35px;
    width: 35px;
        transition: all 0.2s,outline 0s;
}
.rate:not(:checked) > label:hover,
.rate:not(:checked) > label:hover ~ label {
    color: #000;  
    background-color: #ffbc00;
    border-radius: 5px;
    height: 35px;
    width: 35px; 
    transform: scale(1.1); 
}
.rate > input:checked + label:hover,
.rate > input:checked + label:hover ~ label,
.rate > input:checked ~ label:hover,
.rate > input:checked ~ label:hover ~ label,
.rate > label:hover ~ input:checked ~ label {
    color: #000;  
    background-color: #ffbc00;
    border-radius: 5px;
    height: 35px;
    width: 35px;
    transform: scale(1.1);
}